  font-weight: bold;
  text-shadow: 1px 1px white;
}

.outRoomGreen{
  margin: 10% 0 0 25%;
  width: 50%;
  background-color: green;
  font-weight: bold;
  text-shadow: 1px 1px white;

}

#textP, #description{
  margin-top: 5%;
  text-indent: 20px;
  font-weight: bold;
  font-size: 1.1em;
  text-shadow: 1px 1px white;
}

//style dla przycisków "na ulicy", i dla targu
#lookAroundStreet, #inRoom, #buy, #sell{
  margin-top: 5%;
  width: 49%;
  height: 70%;
  font-weight: bold;
  text-shadow: 1px 1px white;
}

#lookAroundStreet{
  background: linear-gradient(white, blue);
}

#buy{
  background: linear-gradient(white, green);
}

#inRoom, #sell{
  background: linear-gradient(white, green);
