  font-weight: bold;
  text-shadow: 1px 1px white;
  background: linear-gradient(white, green);
}

#btnsP1{
  width: 100%;
}

//style dla przycisków kupna/sprzedaży na targu
#buyItemMarket, #sellItemMarket{
  width: 100%;
  text-align: center;
}

.btnsMarket{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

#tentM, #bagM, #blanketM, #lampM, #ropeM, #foodM, #daggerM, #woodenStickM, #bucklerM, #saddleM, #crossBowM, #closeM{
  width: 15%;
