  margin-top: 2%;
  font-weight: bold;
  text-shadow: 1px 1px white;
  background: linear-gradient(white, green);
}

#closeM{
  background: linear-gradient(white, red);
}

//styl dla kupionego przedmiotu
#buyed, #itSell{
  text-align: center;
  font-weight: bold;
  color: green;
  text-shadow: 1px 1px white;
  font-size: 1.2em;
}

#noGold{
  text-align: center;
  font-weight: bold;
  color: red;
  text-shadow: 1px 1px white;
