#wardrobe {
  margin: 4% 0 0 1%;
  background: linear-gradient(white, green); }

#chest {
  margin-top: 2%;
  background: linear-gradient(white, green); }

#package {
  margin: 2% 0 0 1%;
  background: linear-gradient(white, gold); }

#takeCoatWardrobe, #takeGoldChest {
  width: 15%;
  height: 5%;
  background: linear-gradient(white, green);
  font-weight: bold;
  margin-left: 1.5%; }

#closeWardrobe, #closeChest {
  width: 15%;
  height: 5%;
  background: linear-gradient(white, red);
  font-weight: bold;
  margin-left: 1.5%; }

.outRoomRed {
  margin: 10% 0 0 25%;
  width: 50%;
  background-color: red;
  font-weight: bold;
  text-shadow: 1px 1px white; }

.outRoomGreen {
  margin: 10% 0 0 25%;
  width: 50%;
  background-color: green;
  font-weight: bold;
  text-shadow: 1px 1px white; }

#textP, #description {
  margin-top: 5%;
  text-indent: 20px;
  font-weight: bold;
  font-size: 1.1em;
  text-shadow: 1px 1px white; }

#lookAroundStreet, #inRoom, #buy, #sell {
  margin-top: 5%;
  width: 49%;
  height: 70%;
  font-weight: bold;
  text-shadow: 1px 1px white; }

#lookAroundStreet {
  background: linear-gradient(white, blue); }

#buy {
  background: linear-gradient(white, green); }

#inRoom, #sell {
  background: linear-gradient(white, green);
  margin-left: 2%; }

#marketPlace, #caravans {
  margin-top: 3%;
  width: 49%;
  height: 4%;
  font-weight: bold;
  text-shadow: 1px 1px white;
  background: linear-gradient(white, green); }

#btnsP1 {
  width: 100%; }

#buyItemMarket, #sellItemMarket {
  width: 100%;
  text-align: center; }

.btnsMarket {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; }

#tentM, #bagM, #blanketM, #lampM, #ropeM, #foodM, #daggerM, #woodenStickM, #bucklerM, #saddleM, #crossBowM, #closeM {
  width: 15%;
  height: 12%;
  margin-top: 2%;
  font-weight: bold;
  text-shadow: 1px 1px white;
  background: linear-gradient(white, green); }

#closeM {
  background: linear-gradient(white, red); }

#buyed, #itSell {
  text-align: center;
  font-weight: bold;
  color: green;
  text-shadow: 1px 1px white;
  font-size: 1.2em; }

#noGold {
  text-align: center;
  font-weight: bold;
  color: red;
  text-shadow: 1px 1px white;
  font-size: 1.2em; }

.btnsSellGreen {
  width: 15%;
  height: 12%;
  margin-top: 2%;
  font-weight: bold;
  text-shadow: 1px 1px white;
  background: linear-gradient(white, green); }

.redBtnPackage {
  width: 15%;
  height: 12%;
  margin-top: 2%;
  font-weight: bold;
  text-shadow: 1px 1px white;
  background: linear-gradient(white, red); }
